      </div>
      <div class="left-options">
        <v-btn icon @click.stop="onCreateSession">
          <v-icon color="grey lighten-1">mdi-plus</v-icon>
        </v-btn>
      </div>
    </div>
    <RecycleScroller
      class="session-list"
      :items="items"
      :item-size="80"
      key-field="id"
      v-slot="{ item }"
    >
      <div class="session-item" v-ripple @click="onSelectSession(item)">
        <div class="session-item--body">
          <div class="session-item--name txtov">{{item.name}}</div>
          <div class="session-item--description txtov">{{item.description}}</div>
        </div>
        <div class="session-item--options">
          <v-btn icon @click.stop="onEditClick(item)">
            <v-icon color="grey lighten-1">mdi-pencil</v-icon>
