    </v-row>-->
    <v-dialog v-model="deleteDialog" max-width="290">
      <v-card v-if="selectedSession">
        <v-card-title class="headline">Do you want to delete the session ?</v-card-title>

        <v-card-text>
          <b>{{selectedSession.name}}</b> will be deleted permanently.
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn color="red darken-1" text @click="onDeleteSession">Yes</v-btn>
          <v-btn color="blue darken-1" text @click="deleteDialog = false">No</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
