<script>
import { SELECT_SESSION, DELETE_SESSION } from "../../store/index";
import * as R from "ramda";
import { createSession } from "../../models/sessions.model";
import { uuidv4 } from "../../utility";
import * as chance from 'chance';

export default {
  data: function() {
    return {
      deleteDialog: false,

      selectedSession: null
    };
  },
  computed: {
    items: function() {
      return this.$store.getters.sessions;
      // return R.range(0, 1000).map(e =>
      //   createSession({
      //     id: uuidv4(),
      //     name: (new chance()).company()
      //   })
      // );
    },
    selectedSessionId: function() {
      return this.$store.getters.selectedSessionId;
    }
  },
  methods: {
    onCreateSession: function() {
      this.$router.push({
        name: "session"
      });
    },
    onEditClick: function(item) {
      if (!item.id) {
        return;
      }
      this.$router.push({
        name: "session_details",
        params: {
          session_id: item.id
        }
      });
    },
    onDeleteClick: function(item) {
      this.deleteDialog = true;
      this.selectedSession = item;
    },
    onDeleteSession: async function() {
      this.deleteDialog = false;
      if (this.selectedSession) {
        this.$store.dispatch(DELETE_SESSION, this.selectedSession);
      }
    },
    onSelectSession: function(item) {
      if (this.selectedSessionId != item.id) {
        this.$store.dispatch(SELECT_SESSION, item.id);
        if (this.selectedSessionId) {
          this.$router.push({
            name: "timer"
          });
        }
      }
    }
    //MARK: METHODS
  }
};
</script>

