export class SessionStorageClass extends IDBServiceClass {
  constructor() {
    super();
  }

  async getAllSession() {
    await this.initDB();
    return this.db.getAll(SESSION_STORE);
  }


  async deleteAllData() {
    await this.initDB();
    await this.db.clear(SESSION_STORE);
  }

  async deleteSession(id) {
    await this.initDB();
    await this.db.delete(SESSION_STORE, id);
  }

  async insertSession(session) {
    await this.initDB();
    await this.db.add(SESSION_STORE, session)
  }

  async getAll() {
    await this.initDB();
    return this.db.getAll(SESSION_STORE);
  }

}

export const SessionStorage = new SessionStorageClass();