export const ExcollapseMixin = () => {
  return {
    methods: {
      excolToggle: function (value, item) {
        this.$emit('update', {
          name: '_collapsed',
          value,
          item,
        })
      },
    }
  }
}

export const EXPANDED_ALL = 'EXPANDED_ALL';
export const COLLAPSED_ALL = 'COLLAPSED_ALL';

export const ExcollapseHostMixin = (item, key) => {
  return {
    data: function () {
      return {
        ex_collapse_state: EXPANDED_ALL,
      }
    },
    computed: {
      nextCollapse() {
        return this.all_collapsed;
      },
      all_collapsed: function() {
        return this[item][key].every(e => e._collapsed);
      }
    },
    methods: {
      onCollapseAll: function () {
        this.ex_collapse_state = COLLAPSED_ALL;
        this[item][key] = this[item][key].map(e => ({
          ...e,
          _collapsed: true,
        }));
      },
      onExpandAll: function () {
        this.ex_collapse_state = EXPANDED_ALL;
        this[item][key] = this[item][key].map(e => ({
          ...e,
          _collapsed: false,
        }));
      }
    }
  }
}