import Vue from 'vue';
import VueRouter from 'vue-router';
import H from '../views/H/H.vue';
import Home from '../views/H/Home.vue';
import Sessions from '../views/H/Sessions.vue';
import Medias from '../views/H/Medias.vue';
import HistoryUI from '../views/H/History.vue';
import Settings from '../views/H/Settings.vue';
import Session from '../views/session.vue';
import Timer from '../views/timer.vue';

Vue.use(VueRouter);

const router = new VueRouter({
  routes: [
    {
      path: '/',
      redirect: '/h',
    },
    {
      path: '/h',
      name: 'h',
      redirect: '/h/home',
      component: H,
      children: [
        {
          path: 'home',
          name: 'home',
          component: Home
        },
        {
          path: 'sessions',
          name: 'sessions',
          component: Sessions
        },
        {
          path: 'medias',
          name: 'medias',
          component: Medias
        },
        {
          path: 'history',
          name: 'history',
          component: HistoryUI,
        },
        {
          path: 'schedule',
          name: 'schedule',
          component: Home
        },
        {
          path: 'settings',
          name: 'settings',
          component: Settings
        },
      ]
    },
    {
      path: '/session',
      name: 'session',
      component: Session
    },
    {
      path: '/session/:session_id',
      name: 'session_details',
      component: Session
    },
    {
      path: '/timer',
      name: 'timer',
      component: Timer
    },
  ]
})

export default router
