<script>
import { openFullscreen } from "./models/index";
import NoSleep from "nosleep.js";
import { mobileCheck } from "./utility";

import 'vue-virtual-scroller/dist/vue-virtual-scroller.css';

export default {
  props: {
    source: String
  },
  mounted: function() {
    this.onResize();
    window.addEventListener("resize", this.onResize);
  },
  data: () => ({
    dialog: false,
    drawer: null,
    items: [
      { icon: "mdi-contacts", text: "Contacts" },
      { icon: "mdi-history", text: "Frequently contacted" },
      { icon: "mdi-content-copy", text: "Duplicates" },
      {
        icon: "mdi-chevron-up",
        "icon-alt": "mdi-chevron-down",
        text: "Labels",
        model: true,
        children: [{ icon: "mdi-plus", text: "Create label" }]
      },
      {
        icon: "mdi-chevron-up",
        "icon-alt": "mdi-chevron-down",
        text: "More",
        model: false,
        children: [
          { text: "Import" },
          { text: "Export" },
          { text: "Print" },
          { text: "Undo changes" },
          { text: "Other contacts" }
        ]
      },
      { icon: "mdi-settings", text: "Settings" },
      { icon: "mdi-message", text: "Send feedback" },
      { icon: "mdi-help-circle", text: "Help" },
      { icon: "mdi-cellphone-link", text: "App downloads" },
      { icon: "mdi-keyboard", text: "Go to the old version" }
    ],

    nosleep: null,
    isFullScreen: false,
    isMobile: mobileCheck()
  }),
  watch: {
    windowHeight: {
      immediate: true,
      handler(to, from) {
        console.log(
          "Log: screen.height",
          screen.height,
          this.windowHeight,
          window.innerHeight
        );
        this.isFullScreen = this.windowHeight == screen.height;
      }
    }
  },
  computed: {
    showFullscreenButton: function() {
      return this.isMobile && !this.isFullScreen;
    }
  },
  methods: {
    onAppClick: function() {},
    onResize: function() {
      this.windowHeight = window.innerHeight;

      this.isFullScreen = this.windowHeight == screen.height;
      console.log("Log: window.innerHeight", window.innerHeight);
    },
    onFullscreenClick: function() {
      openFullscreen();
      if (!this.nosleep) {
        // this.nosleep = new NoSleep();
        // this.nosleep.enable();
      }
    }
    //MARK: Methods
  },
  beforeDestroy: function() {
    window.removeEventListener("resize", this.onResize);
  }
};
</script>

