<template>
  <v-col class="exercise" :class="{
    'collapsed': exercise._collapsed
  }">
    <GxrTitle
      entity_type="E"
      :gxr="exercise"
      @bar_click="excolToggle(!exercise._collapsed, exercise)"
      @delete="onExerciseDelete"
    />
    <div class="form-group">
      <v-text-field label="Name" outlined :value="exercise.name" @input="e => onChange('name', e)" hide-details></v-text-field>
      <!-- <v-text-field label="Desciption" outlined :value="exercise.description" @input="e => onChange('description', e)" hide-details></v-text-field> -->
    </div>
    <v-row>
      <Duration label="Duration" :value="exercise.duration" @update="e => onChange('duration', e)"/>
      <Duration label="Recovery" :value="exercise.recovery"  @update="e => onChange('recovery', e)" :step="5"/>
      <NumberInput label="Repeat" :value="exercise.repeat" @update="e => onChange('repeat', e)"/>
    </v-row>
  </v-col>
</template>

<script>
import Duration from "../components/duration";
import NumberInput from "../components/number-input";
import { ExcollapseMixin } from "../mixins/excollapse.mixin";
import GxrTitle from "./gxr-title";

export default {
  components: {
    Duration,
    NumberInput,
    GxrTitle
  },
  props: ["exercise"],
  mixins: [ExcollapseMixin()],
  data: function() {
    return {};
  },
  methods: {
    onExerciseDelete: function() {
      this.$emit("delete", this.exercise);
    },
    onChange: function(name, value) {
      this.$emit('update', {
        name,
        value,
        item: this.exercise
      });
    }
    //MARK: Methods
  }
};
</script>
