<template>
  <v-list-item>
    <template>
      <v-list-item-content>
        <v-list-item-title>{{title}}</v-list-item-title>
        <v-list-item-subtitle> {{subtitle}} </v-list-item-subtitle>
      </v-list-item-content>
      <v-list-item-action>
        <v-switch :input-value="settings[path]"  @change="onChange"/>
      </v-list-item-action>
    </template>
  </v-list-item>
</template>

<script>
export default {
  props: ['settings', 'title', 'subtitle', 'path'],
  methods: {
    onChange: function(value) {
      this.$emit('changed', {
        name: this.path,
        value
      });
    }
  }
}
</script>