<template>
  <v-row class="duration-picker" pa-2>
    <span class="label">{{label}}</span>
    <v-text-field label="00" :value="valueObj.minutes" hide-details flat solo type="number"></v-text-field>
    <span class="separator">:</span>
    <v-text-field label="00" :value="valueObj.seconds" hide-details flat solo type="number"></v-text-field>
    <Incdec @decrease="onDecrease" @increase="onIncrease" />
  </v-row>
</template>

<script>
import Incdec from "../components/incdec";
import { SessionFuncs } from "../models/sessions.model";

const DEFAULT_STEP_SIZE = 15;
const MIN = 0;
const MAX = Number.MAX_SAFE_INTEGER;

export default {
  components: {
    Incdec
  },
  props: {
    value: {
      default: 0
    },
    label: {
      default: "Duration"
    },
    step: {
      default: DEFAULT_STEP_SIZE,
    },
    min: {
      default: MIN
    },
    max: {
      default: MAX
    }
  },
  computed: {
    valueObj() {
      return SessionFuncs.getDurationObj(this.value);
    }
  },
  methods: {
    onDecrease: function() {
      const value = this.value - this.step;

      this.$emit("update", value >= this.min ? value : this.min);
    },
    onIncrease: function() {
      const value = this.value + this.step;

      this.$emit("update", value <= this.max ? value : this.max);
    }
  }
};
</script>

