import { IDBServiceClass, MEDIA_STORE } from "./idb.service";
import { uuidv4 } from "../utility";



export const MediaService = new (class MediaServiceClass extends IDBServiceClass {
  constructor() {
    super();
  }

  async getAllMedia() {
    await this.initDB();
    return this.db.getAll(MEDIA_STORE);
  }

  async insertMedia(media = createMedia()) {
    await this.initDB();
    await this.db.add(MEDIA_STORE, media);
  }

  async deleteMedia(id) {
    await this.db.delete(MEDIA_STORE, id);
  }

})()


