import { uuidv4 } from "../utility";
import { DefaultSettings } from "./settings.model";

export const SETTINGS_STORE = 'SETTINGS_STORE';


export const SettingsService = new (class SettingsServiceClass {
  constructor() {

  }

  getSettings() {
    return JSON.parse(localStorage.getItem(SETTINGS_STORE) || JSON.stringify(DefaultSettings));
  }

  setSettings(value = {}) {
    localStorage.setItem(SETTINGS_STORE, JSON.stringify(value));
  }

  setSingleSettings(name, value) {
    settings = this.getSettings();
    settings[name] = value;
    this.setSettings()
  }

})()


