<script>
import { DEMO_SESSION_LIST } from "../../models/sessions.model";
import { SELECT_SESSION, SET_SESSIONS, SET_HISTORY, SET_MEDIAS } from "../../store/index";
import { SessionStorage } from "../../models/session-storage";
import { HistoryService } from '../../models/history.model';
import { MediaService } from '../../models/media.service';

// @ is an alias to /src

export default {
  name: "Home",
  components: {},
  mounted: async function() {
    await SessionStorage.initDB();
    const data = await SessionStorage.getAll();
    const history = await HistoryService.getAllHistory();
    const media = await MediaService.getAllMedia();

    if (data.length > 0) {
      this.$store.commit(SET_SESSIONS, data);
      if (!this.selectedSessionId) {
        this.$store.dispatch(SELECT_SESSION, data[0]?.id);
      }
    }

    if (history) {
      this.$store.commit(SET_HISTORY, history);
    }
    if (media) {
      this.$store.commit(SET_MEDIAS, media);
    }
  },
  data: function() {
    return {
      
    };
  },
  computed: {
    selectedSessionId: function() {
      return this.$store.getters.selectedSessionId;
    },
    activeBtn: function() {
      console.log(this.$router.currentRoute)
      return this.$router.currentRoute.name;
    }
  },
  methods: {
    onBottomBtnClick: function(route) {
      this.$router.push({
        path: `/h/${route}`
      })
    }
    //MARK: Methods
  }
};
</script>


