<template>
  <v-container class="fill-height timer" :class="timerClass">
    <audio ref="tick_sound" src="alert00.mp3"></audio>
    <audio ref="start_sound" src="alert01.mp3"></audio>

    <span class="ex-title txtov">{{exTitle}}</span>
    <div class="clock">
      <div class="lap-panel-container">
        <div class="lap-panel">
          <transition name="slide-fade">
            <div
              class="current-exercise--lap"
              v-if="SessionFuncs.isValidExGroup(currentExercise) && currentExercise.lapCount[1] > 1"
            >
              <div class="value">Lap {{currentExercise.lapCount[0]}}/{{currentExercise.lapCount[1]}}</div>
            </div>
          </transition>
          <transition name="slide-fade">
            <div
              class="current-group--lap"
              v-if="SessionFuncs.isValidExGroup(currentGroup) && currentGroup.lapCount[1] > 1"
            >
              <div
                class="value"
                v-if="SessionFuncs.isValidExGroup(currentGroup) && currentGroup.lapCount[1] > 1"
              >Group {{currentGroup.lapCount[0]}}/{{currentGroup.lapCount[1]}}</div>
            </div>
          </transition>
        </div>
      </div>
      <div class="time">
        <v-progress-circular
          size="130"
          v-if="SessionFuncs.isValidExGroup(currentExercise)"
          :value="currentExercise.activeProgress[2]"
          rotate="270"
          width="7"
          color="blue lighten-1"
        ></v-progress-circular>
        <Minute :value="mainCountDown" />
      </div>
    </div>
    <div class="control-panel" v-if="isWarmpupDone">
      <v-btn class="mx-2 stop-button" fab depressed small color="default" @click="onSpeedupClick">
        <v-icon>mdi-skip-forward</v-icon>
      </v-btn>
      <v-btn
        v-if="clockInterval"
        class="mx-2 pp-button"
        fab
        depressed
        small
        color="default"
        @click="onPauseClick"
      >
        <v-icon>mdi-pause</v-icon>
      </v-btn>
      <v-btn v-else class="mx-2 pp-button" fab depressed small color="default" @click="onPlayClick">
        <v-icon>mdi-play</v-icon>
      </v-btn>
      <v-btn class="mx-2 stop-button" fab depressed small color="default" @click="onStopClick">
        <v-icon>mdi-stop</v-icon>
      </v-btn>
      <transition name="up-fade">
        <div
          v-if="isWarmpupDone && isCountdownActive && nextExercise.id != currentExercise.id"
          class="next-exercise txtov"
        >Next Exercise - {{nextExercise.name}}</div>
      </transition>
    </div>
    <div class="current-exercise" :class="cexClass">
      <template v-if="SessionFuncs.isValidExGroup(currentExercise)">
        <div class="exercise-name">
          {{currentExercise.name}}
          <span class="name-label">- Current Exericse</span>
        </div>
        <TimeProgress id="active" :progress="currentExercise.activeProgress" />
        <TimeProgress id="lap" :progress="currentExercise.lapProgress" />
        <TimeProgress id="total" :progress="currentExercise.totalProgress" />
      </template>
    </div>
    <div class="current-group" :class="cgrClass">
      <template v-if="SessionFuncs.isValidExGroup(currentGroup)">
        <div class="group-name">
          {{currentGroup.name}}
          <span class="name-label">- Current Group</span>
        </div>
        <TimeProgress id="active" :progress="currentGroup.activeProgress" />
        <TimeProgress id="lap" :progress="currentGroup.lapProgress" />
        <TimeProgress id="total" :progress="currentGroup.totalProgress" />
      </template>
    </div>
    <div class="media-player-container">
      <video id="exercise-media-player"></video>
    </div>
  </v-container>
</template>

<script>
import {
  createSession,
  SessionFuncs,
  isRest,
  isValidExGroup,
  isExerciseActive
} from "../models/sessions.model";
import Minute from "../components/minute";
import TimeProgress from "../components/time-pg-bar";
import { HistoryService, createHistory } from "../models/history.model";
import * as R from "ramda";
import Plyr from "plyr";
import "plyr/";

let funcTimes = [];

export default {
  name: "Timer",
  components: {
    Minute,
    TimeProgress
  },
  data: function() {
    return {
      time: 0,
      getExGroup: SessionFuncs.getExGroupTimerGetter(this.selectedSession),
      exGroup: SessionFuncs.getExGroupTimerGetter(this.selectedSession)(0),
      clockInterval: null,
      warmupDuration: 5,
      warmpupCounter: 0,
      warmupInterval: null,
      SessionFuncs: SessionFuncs,

      countdownTime: 4,

      mediaPlayer: null
    };
  },
  mounted: function() {
    if (this.settings.player_active) {
      this.initializeMedia();
    }

    if (!this.$store.getters.selectedSession) {
      this.$router.back();
    }
    console.log(this.selectedSession);
    this.getExGroup = SessionFuncs.getExGroupTimerGetter(this.selectedSession);
    this.startWarmup();
  },
  destroyed: function() {
    this.stopTimer();
    this.stopWarmup();
    this.insertHistory();
  },
  computed: {
    settings: function() {
      return this.$store.getters.settings;
    },
    medias() {
      return this.$store.getters.medias || [];
    },
    selectedSession() {
      return this.$store.getters.selectedSession || createSession({});
    },
    timeLeft: function() {
      return SessionFuncs.getDurationObj(
        this.selectedSession.total_duration - this.time
      );
    },
    currentExercise() {
      return this.exGroup?.exercise_state || {};
    },
    currentGroup() {
      return this.exGroup?.group_state || {};
    },
    warmupLeft: function() {
      return this.warmupDuration - this.warmpupCounter;
    },
    mainCountDown: function() {
      return this.currentExerciseCountdown > -1
        ? this.currentExerciseCountdown
        : this.currentGroupCountdown > -1
        ? this.currentGroupCountdown
        : -1;
    },
    currentExerciseCountdown() {
      return this.isWarmpupDone
        ? this.currentExercise?.active_time_left || -1
        : this.warmupLeft;
    },
    currentGroupCountdown() {
      return this.isWarmpupDone
        ? this.currentGroup?.active_time_left || -1
        : this.warmupLeft;
    },
    nextExercise() {
      return this.exGroup?.next_exercise || {};
    },
    timerClass: function() {
      // console.log(
      //   this.exGroup?.exercise_state?.lap_state,
      //   this.exGroup?.exercise_state?.lap_state == "recovery"
      // );
      return {
        a_rest:
          this.exGroup?.exercise_state?.lap_state == "recovery" ||
          this.exGroup?.group_state?.lap_state == "recovery" ||
          isRest(this.exGroup?.exercise_state),
        a_exercise:
          this.exGroup?.exercise_state?.lap_state == "exercise" &&
          !isRest(this.exGroup?.exercise_state)
      };
    },
    cgrClass: function() {
      return {
        hide: !SessionFuncs.isValidExGroup(this.currentGroup)
      };
    },
    cexClass: function() {
      return {
        hide: !SessionFuncs.isValidExGroup(this.currentExercise)
      };
    },
    isWarmpupDone: function() {
      return this.warmpupCounter >= this.warmupDuration;
    },
    isCountdownActive: function() {
      return (
        this.currentExerciseCountdown <= this.countdownTime &&
        this.currentExerciseCountdown > -1
      );
    },
    exTitle: function() {
      return isValidExGroup(this.currentExercise)
        ? isExerciseActive(this.currentExercise)
          ? this.currentExercise.name
          : "Rest"
        : isValidExGroup(this.currentGroup)
        ? isExerciseActive(this.currentGroup)
          ? this.currentGroup.name
          : "Rest"
        : "Session : " + this.selectedSession.name;
    }
  },
  methods: {
    startWarmup: function() {
      this.warmupInterval = setInterval(() => {
        this.tickWarmpup();
      }, 1000);
    },
    stopWarmup: function() {
      clearInterval(this.warmupInterval);
    },
    tickWarmpup: function() {
      this.warmpupCounter++;
      this.playTick();
      if (this.isWarmpupDone) {
        this.stopWarmup();
        this.startClock();
      }
    },
    startClock: function(interval = 1000) {
      this.tickClock();
      this.clockInterval = setInterval(() => {
        this.tickClock();
      }, interval);
    },
    playTick: function() {
      // console.log(this.currentExerciseCountdown, this.countdownTime, this.isCountdownActive);
      if (this.isCountdownActive) {
        if (this.currentExerciseCountdown == 1) {
          this.$refs["start_sound"]?.play();
        } else {
          this.$refs["tick_sound"]?.play();
        }
      }

      this.mediaPlayerUpdate();
    },
    tickClock: function() {
      this.time++;
      const start = performance.now();
      this.exGroup = this.getExGroup(this.time);
      const end = performance.now();
      funcTimes.push(end - start);

      this.playTick();

      if (!this.exGroup) {
        this.stopTimer();
      } else {
        // console.log(`
        // ${this.exGroup?.groupInfo}
        // ${this.exGroup?.exerciseInfo}
        // `);
      }

      // FREE
      if (this.time >= this.selectedSession.total_duration) {
        this.stopTimer();
      }
      // if (this.time >= 6) {
      //   clearInterval(this.clockInterval);
      // }
    },
    stopClock: function() {
      clearInterval(this.clockInterval);
    },
    stopTimer: function() {
      this.stopClock();
      this.clockInterval = null;
      this.$router.push({
        name: "h"
      });

      console.log(
        funcTimes.reduce((accm, cur) => accm + cur, 0) / funcTimes.length,
        funcTimes
      );
    },
    onStopClick: function() {
      this.stopTimer();
    },
    onPauseClick: function() {
      this.stopClock();
      this.clockInterval = null;
    },
    onPlayClick: function() {
      this.startClock();
    },
    onSpeedupClick: function() {
      this.stopClock();
      this.startClock(25);
    },
    insertHistory: function() {
      console.log(this.selectedSession);
      if (SessionFuncs.isValidExGroup(this.selectedSession) && this.time > 0) {
        HistoryService.insertHistory(
          createHistory({
            session_id: this.selectedSession.id,
            session_name: this.selectedSession.name,
            session_duration: this.selectedSession.total_duration,
            session_completion: this.time
          })
        );
      }
    },

    mediaPlayerUpdate: function() {
      if (this.isCountdownActive && this.settings.player_active) {
        if (this.currentExerciseCountdown == 1) {
          console.log({
            "this.currentExerciseCountdown == 1":
              this.currentExerciseCountdown == 1,
            "this.isWarmpupDone": !this.isWarmpupDone
          });
          if (!this.isWarmpupDone) {
            if (this.settings.player_play_during_exercise) {
              this.playMedia();
            }
          }
          if (this.timerClass.a_rest) {
            if (this.settings.player_play_during_exercise) {
              this.playMedia();
            } else {
              this.pauseMedia();
            }
          }
          if (this.timerClass.a_exercise) {
            if (this.settings.player_play_during_rest) {
              this.playMedia();
            } else {
              this.pauseMedia();
            }
          }
        }
        if (this.currentExerciseCountdown == 4) {
          this.pauseMedia();
        }
      }
    },
    playMedia: function() {
      console.log('this.mediaPlayer.play();');
      this.mediaPlayer.play();
      if (this.settings.player_play_in_fullscreen) {
        if (
          this.mediaPlayer.fullscreen.enabled &&
          !this.mediaPlayer.fullscreen.active
        ) {
          this.mediaPlayer.fullscreen.enter();
        }
      }
    },
    pauseMedia: function() {
      console.log("Log: pauseMedia");
      this.mediaPlayer.pause();
      if (this.mediaPlayer.fullscreen.enabled) {
        this.mediaPlayer.fullscreen.exit();
      }
    },
    initializeMedia: function() {
      this.mediaPlayer = new Plyr("#exercise-media-player");
      this.mediaPlayer.source = this.medias[0].source;

      this.mediaPlayer.on("ready", event => {
        this.mediaPlayerUpdate();
      });
    }
    //MARK: Methods
  }
};
</script> 


<style lang="scss">
