    --color-5: var(--exercise-color-5); // Button

    --shadow-1: var(--exercise-shadow-1);
    --shadow-2: var(--exercise-shadow-2);
  }
  &.a_rest {
    --color-2: var(--rest-color-2); // Timer Back
    --color-3: var(--rest-color-3); // Ex
    --color-4: var(--rest-color-4); // Goup
    --color-5: var(--rest-color-5); // Button

    --shadow-1: var(--rest-shadow-1);
    --shadow-2: var(--rest-shadow-2);
  }

