<template functional>
  <div class="gxr-title" @click.capture="e => $options.methods.barClick(listeners, props.gxr, e)">
    <v-btn depressed class="excol-button">
      <v-icon v-if="props.gxr._collapsed">mdi-plus</v-icon>
      <v-icon v-else>mdi-minus</v-icon>
    </v-btn>
    <div class="main-title">
      <div class="type">{{props.entity_type}}</div>
      <div class="main-title--text txtov">{{props.gxr.name}}</div>
    </div>
    <div class="duration">{{$options.getDurationString(props.gxr.total_duration)}}</div>
    <v-btn depressed class="delete-button" @click="listeners.delete">
      <v-icon>mdi-delete-outline</v-icon>
    </v-btn>
  </div>
</template>

<script>
import { SessionFuncs } from "../models/sessions.model";
import { getParent } from "../utility";

export default {
  getDurationString: SessionFuncs.getDurationString,
  methods: {
    barClick: function(listeners, gxr, event) {
      if (gxr._collapsed) {
        // event.target.scrollIntoView({ block: "center" });
        console.time("GETEL");
        const element = event?.path?.find(e =>
          e?.classList?.contains("gxr-title")
        );
        console.timeEnd("GETEL");
        if (!element) {
          return;
        }
        console.log(element, getParent(element, 2), getParent(element, 3));
        // getParent(element, 3).scrollIntoView({
        //   behavior: "smooth",
        //   block: "start"
        // });

        setTimeout(() => {
          getParent(element, 2).scrollIntoView({
            behavior: "smooth",
            block: "start"
          });
        }, 200);
      }
      listeners.bar_click(event);
    }
  }
};
</script>