<template>
  <v-container class="fill-height session" :class="{
    'COLLAPSED_ALL': all_collapsed
  }">
    <div class="session-top-bar">
      <v-btn depressed @click="onCancel">Cancel</v-btn>
      <div class="duration">
        <v-icon>mdi-timer-outline</v-icon>
        <div class="duration--text">
          <div>{{SessionFuncs.getDurationString(session.total_duration)}}</div>
        </div>
      </div>
      <v-btn depressed @click="onSaveClick">Save</v-btn>
    </div>
    <div class="session-content">
      <v-col class="form-group" style="flex: 1">
        <v-text-field label="Name" outlined v-model="session.name" hide-details></v-text-field>
        <v-text-field label="Desciption" outlined v-model="session.description" hide-details></v-text-field>
      </v-col>
      <v-row class="allcolexpand-buttons">
        <!-- <div class="duration">
        <v-icon>mdi-timer-outline</v-icon>
        <div class="duration--text">
          <div>{{SessionFuncs.getDurationString(session.total_duration)}}</div>
        </div>
        </div>-->
        <v-btn outlined x-small>
          <span v-if="all_collapsed" @click="onExpandAll">Expand All</span>
          <span v-else @click="onCollapseAll">Collapse All</span>
        </v-btn>
        <!-- <v-btn outlined x-small @click="onCollapseAll">Collapse All</v-btn> -->
      </v-row>
      <div class="exgroup-list" ref="exgroup-list">
        <v-row v-for="exgroup of session.exercise_groups" :key="exgroup.id">
          <GroupComponent
            v-if="exgroup.type == 'group'"
            :group="exgroup"
            @add_exercise="onExerciseAddedToGroup"
            @add_rest="onRestAddedToGroup"
            @update="onExGroupUpdate"
            @delete="onExGroupDeleted"
          />
          <ExerciseComponent
            v-if="exgroup.type == 'exercise'"
            :exercise="exgroup"
            @update="onExGroupUpdate"
            @delete="onExGroupDeleted"
          ></ExerciseComponent>
          <RestComponent
            v-if="exgroup.type == 'rest'"
            :rest="exgroup"
            @update="onExGroupUpdate"
            @delete="onExGroupDeleted"
          ></RestComponent>
        </v-row>
      </div>
      <v-row class="adding-panel">
        <div class="subtitle">Add to Session</div>
        <div class="buttons">
          <v-btn @click="onAddGroup" class="add-group-button">
            <v-icon left>mdi-plus</v-icon>Group
          </v-btn>
          <v-btn @click="onAddExercise" class="add-exercise-button">
            <v-icon left>mdi-plus</v-icon>Exercise
          </v-btn>
          <v-btn @click="onAddRest" class="add-rest-button">
            <v-icon left>mdi-plus</v-icon>Rest
          </v-btn>
        </div>
      </v-row>
    </div>
  </v-container>
</template>

<script>
import {
  createSession,
  DEMO_SESSION_LIST,
  createGroup,
  createExercise,
  createRest,
  SessionFuncs
} from "../models/sessions.model";
import GroupComponent from "../components/group";
import ExerciseComponent from "../components/exercise";
import RestComponent from "../components/rest";
import { uuidv4 } from "../utility";
import { ExcollapseHostMixin } from "../mixins/excollapse.mixin";
import { SessionStorage } from "../models/session-storage";
import { mapGetters } from "vuex";

export default {
  name: "Home",
  components: {
    GroupComponent,
    ExerciseComponent,
    RestComponent
  },
  mixins: [ExcollapseHostMixin("session", "exercise_groups")],
  data: function() {
    return {
      // session: createSession(DEMO_SESSION_LIST[1]),
      session: createSession({
        id: uuidv4(),
        exercise_groups: []
      }),
      SessionFuncs: SessionFuncs
    };
  },
  watch: {
    $route: {
      immediate: true,
      handler: function(to, from) {
        console.log("Log: to", to);
        if (to?.params?.session_id) {
          const session = this.$store.getters.sessions.find(
            s => s.id == to.params.session_id
          );
          if (!session) {
            this.$router.push({
              name: "sessions"
            });
          } else {
            this.session = session;
          }
        }
      }
    }
  },
  computed: {
    ...mapGetters({
      selectedSession: "selectedSession"
    })
  },
  methods: {
    onCreateSession: function() {},
    onExerciseAddedToGroup: function(group, nextCollapse) {
      this.onExGroupUpdate({
        name: "exercises",
        value: [
          ...group.exercises,
          createExercise({
            id: uuidv4(),

            _collapsed: nextCollapse
          })
        ],
        item: group
      });
    },
    onRestAddedToGroup: function(group, nextCollapse) {
      this.onExGroupUpdate({
        name: "exercises",
        value: [
          ...group.exercises,
          createRest({ id: uuidv4(), _collapsed: nextCollapse })
        ],
        item: group
      });
    },
    scrollBottom: function() {
      this.$nextTick(() => {
        if (this.$refs["exgroup-list"]) {
          this.$refs["exgroup-list"].scrollTop = this.$refs[
            "exgroup-list"
          ]?.scrollHeight;
        }
      });
    },
    onAddGroup: function() {
      this.updateGroup("exercise_groups", [
        ...this.session.exercise_groups,
        createGroup({
          id: uuidv4(),
          exercises: [],

          _collapsed: this.nextCollapse
        })
      ]);
      this.scrollBottom();
    },
    onAddExercise: function() {
      this.updateGroup("exercise_groups", [
        ...this.session.exercise_groups,
        createExercise({
          id: uuidv4(),

          _collapsed: this.nextCollapse
        })
      ]);
      this.scrollBottom();
    },
    onAddRest: function() {
      this.updateGroup("exercise_groups", [
        ...this.session.exercise_groups,
        createRest({ id: uuidv4(), _collapsed: this.nextCollapse })
      ]);
      this.scrollBottom();
    },

    onExGroupUpdate: function({ name, value, item }) {
      console.log({ name, value, item });
      this.updateGroup(
        "exercise_groups",
        this.session.exercise_groups.map(e => {
          if (e.id == item.id) {
            return {
              ...e,
              [name]: value
            };
          } else {
            return e;
          }
        })
      );
    },
    updateGroup: function(name, value) {
      this.session = createSession({
        ...this.session,
        [name]: value
      });
    },
    onExGroupDeleted: function(item) {
      // this.session.exercise_groups = this.session.exercise_groups.filter(e => {
      //   if (e.id == item.id) {
      //     return false;
      //   } else {
      //     return true;
      //   }
      // });
      this.updateGroup(
        "exercise_groups",
        this.session.exercise_groups.filter(e => {
          if (e.id == item.id) {
            return false;
          } else {
            return true;
          }
        })
      );
    },

    onCancel: function() {
      this.$router.push({
        name: "sessions"
      });
    },
    onSaveClick: function() {
      SessionStorage.insertSession(this.session);
      this.onCancel();
    }
    //MARK: Methods
  }
};
</script>


