import { getDB, IDBServiceClass, HISTORY_STORE } from "./idb.service"

export const createHistory = ({
  date = (new Date()).toISOString(),
  session_id = -1,
  session_name = '',
  session_duration = 0,
  session_completion = 0,
}) => {
  return {
    date,
    session_id,
    session_name,
    session_duration,
    session_completion,
  }
}

export const HistoryService = new (class HistoryServiceClass extends IDBServiceClass {
  constructor() {
    super();
  }

  async getAllHistory() {
    await this.initDB();
    return this.db.getAll(HISTORY_STORE);
  }

  async insertHistory(history = createHistory()) {
    console.log("Log: HistoryServiceClass -> insertHistory -> history", history)
    await this.initDB();
    await this.db.add(HISTORY_STORE, history);
  }

  async deleteHistory(date) {
    await this.db.delete(HISTORY_STORE, date);
  }

}) ()