<template>
  <div class="h-child home">
    <div class="session-quick-list">
      <v-col cols="12">
        <v-list flat>
          <v-list-item v-for="item in items" :key="item.key" @click="onSelectSession(item)">
            <v-list-item-action>
              <v-checkbox
                :input-value="item.id == selectedSessionId"
                color="primary"
                @click="onSelectSession(item)"
              ></v-checkbox>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>{{item.name}}</v-list-item-title>
              <v-list-item-subtitle>{{item.description}}</v-list-item-subtitle>
            </v-list-item-content>
            <!-- <v-list-item-avatar>
              <v-img :src="item.avatar"></v-img>
            </v-list-item-avatar>-->
          </v-list-item>
        </v-list>
      </v-col>
    </div>
    <v-row style="flex: 0">
      <!-- <v-col align="center">
        <v-btn @click="onEditClick" depressed style="background-color: transparent">Edit</v-btn>
      </v-col>-->
      <v-col align="center">
        <v-btn x-medium color="deep-purple accent-4" outlined depressed @click="onEditClick">
          <v-icon dark>mdi-pencil</v-icon>Edit
        </v-btn>
      </v-col>
    </v-row>
    <v-row></v-row>
    <v-row style="flex: 1">
      <v-col align="center">
        <v-btn
          x-large
          color="deep-purple accent-4"
          style="color: white; font-size: 24px; height: 70px; width: 140px"
          @click="onStartClick"
        >Start</v-btn>
      </v-col>
    </v-row>
    <v-row>
      <!-- <v-col align="center">
        <v-text-field label="Source" outlined v-model="videoSource" hide-details></v-text-field>
      </v-col> -->
    </v-row>
    <v-row>
      <v-col align="center">
        <v-btn x-medium color="deep-purple accent-4" outlined @click="onCreateSession">
          <v-icon dark>mdi-plus</v-icon>Create Session
        </v-btn>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { SELECT_SESSION } from "../../store/index";

export default {
  computed: {
    items: function() {
      return this.$store.getters.sessions.slice(0, 3);
    },
    selectedSessionId: function() {
      return this.$store.getters.selectedSessionId;
    },
    videoSource: {
      get() {
        return this.$store.getters.videoSource;
      },
      set(value) {
        this.$store.commit("SET_SOURCE", value);
      }
    }
  },
  methods: {
    onCreateSession: function() {
      this.$router.push({
        name: "session"
      });
    },
    onSelectSession: function(item) {
      if (this.selectedSessionId != item.id) {
        this.$store.dispatch(SELECT_SESSION, item.id);
      } else {
        // this.$store.dispatch(SELECT_SESSION, null);
      }
    },
    onStartClick: function() {
      if (this.selectedSessionId) {
        this.$router.push({
          name: "timer"
        });
      }
    },
    onEditClick: function() {
      if (!this.selectedSessionId) {
        return;
      }
      this.$router.push({
        name: "session_details",
        params: {
          session_id: this.selectedSessionId
        }
      });
    }
  }
};
</script>

<style lang="scss">
.home {
  .session-quick-list {
    max-height: 252px;
    overflow-y: auto;
  }
}
</style>