import { uuidv4 } from "../utility";


export const createMedia = ({
  id = '',
  title = 'Example title',
  src = 'bTqVqk7FSmY',
  provider = 'youtube',
  type = 'video',
}) => {
  return {
    id,
    title,
    type,
    src,
    provider,

    source: {
      type,
      sources: [
        {
          src,
          provider,
        },
      ],
    }

  }
}





