<template>
  <div class="h-child sessions">
    <div class="top-bar">
      <div class="right-options"></div>
      <div class="left-options">
        <v-btn icon @click.stop="onAddMediaClick">
          <v-icon color="grey lighten-1">mdi-plus</v-icon>
        </v-btn>
      </div>
    </div>
    <RecycleScroller
      class="media-list"
      :items="items"
      :item-size="80"
      key-field="id"
      v-slot="{ item }"
    >
      <div class="media-item" v-ripple @click="onEditClick(item)">
        <div class="media-item--body">
          <div class="media-item--name txtov">{{item.title}}</div>
          <div class="media-item--description txtov">{{item.src}}</div>
        </div>
        <div class="media-item--options">
          <!-- <v-btn icon @click.stop="onEditClick(item)">
            <v-icon color="grey lighten-1">mdi-pencil</v-icon>
          </v-btn>-->
          <v-btn icon @click.stop="onDeleteClick(item)">
            <v-icon color="grey lighten-1">mdi-delete</v-icon>
          </v-btn>
        </div>
      </div>
    </RecycleScroller>
    <v-dialog v-model="mediaDialog" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline">{{editDialog ? 'Edit': 'Add'}} Media</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-text-field label="Title" v-model="media.title"></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Source*"
                  v-model="media.src"
                  required
                  @change="onSourceChanged"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-select :items="['html5', 'youtube', 'vimeo']" label="Provider"></v-select>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="onCloseMediaDialog">Close</v-btn>
          <v-btn color="blue darken-1" text @click="onAddMediaDone">{{editDialog ?'Edit': 'Add'}}</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="deleteDialog" max-width="290">
      <v-card v-if="media">
        <v-card-title class="headline">Do you want to delete the Media ?</v-card-title>

        <v-card-text>
          <b>{{media.title || media.src}}</b> will be deleted permanently.
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn color="green darken-1" text @click="deleteDialog = false">No</v-btn>
          <v-btn color="blue darken-1" text @click="onDeleteMedia">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import {
  SELECT_SESSION,
  DELETE_SESSION,
  INSERT_MEDIA,
  REMOVE_MEDIA
} from "../../store/index";
import * as R from "ramda";
import { createSession } from "../../models/sessions.model";
import { uuidv4 } from "../../utility";
import * as chance from "chance";
import { createMedia } from "../../models/media.model";

export default {
  data: function() {
    return {
      mediaDialog: false,

      media: createMedia({
        id: uuidv4()
      }),
      deleteDialog: null,

      editDialog: false,
    };
  },
  computed: {
    items: function() {
      return this.$store.getters.medias;
    }
  },
  methods: {
    onAddMediaClick: function() {
      this.mediaDialog = true;
      this.editDialog = false;
      this.media = createMedia({
        id: uuidv4()
      });
    },
    onEditClick: function(item) {
      if (!item.id) {
        return;
      }
      this.media = item;
      this.mediaDialog = true;
      this.editDialog = true;
    },
    onDeleteClick: function(item) {
      this.deleteDialog = true;
      this.media = item;
    },

    onSourceChanged: function(value) {},
    onAddMediaDone: function() {
      this.media = createMedia({
        ...this.media
      });
      this.$store.dispatch(INSERT_MEDIA, this.media);
      this.mediaDialog = false;
    },
    onDeleteMedia: function() {
      this.deleteDialog = false;
      this.$store.dispatch(REMOVE_MEDIA, this.media);
    },
    onCloseMediaDialog: function() {
      this.mediaDialog = false;
      this.editDialog = false;
    }
    //MARK: METHODS
  }
};
</script>

