import Vue from 'vue'
import Vuex from 'vuex'
import { DEMO_SESSION_LIST } from '../models/sessions.model';
import { SessionStorage } from '../models/session-storage';
import { MediaService } from '../models/media.service';
import { SettingsService } from '../models/setting.service';


Vue.use(Vuex)
export const SELECT_SESSION = 'SELECT_SESSION';
export const SET_SESSIONS = 'SET_SESSIONS';
export const SET_SOURCE = 'SET_SOURCE';
export const SET_HISTORY = 'SET_HISTORY';
export const DELETE_SESSION = 'DELETE_SESSION';

export const SET_MEDIAS = 'SET_MEDIAS';
export const UPDATE_MEDIA = 'UPDATE_MEDIA';
export const INSERT_MEDIA = 'INSERT_MEDIA';
export const REMOVE_MEDIA = 'REMOVE_MEDIA';

export const GET_SETTINGS = 'GET_SETTINGS';
export const SET_SETTINGS = 'SET_SETTINGS';
export const SET_SINGLE_SETTINGS = 'SET_SINGLE_SETTINGS';

export default new Vuex.Store({
  state: {
    selectedSessionId: 0,
    sessions: DEMO_SESSION_LIST,
    history: [],
    medias: [],
    source: '',

    settings: SettingsService.getSettings(),
  },
  mutations: {
    [SET_SESSIONS](state, payload) {
      state.sessions = payload;
    },
    [SET_MEDIAS](state, payload) {
      state.medias = payload;
    },
    [SET_HISTORY](state, payload) {
      state.history = payload;
    },
    [SET_SOURCE](state, payload) {
      state.source = payload;
    },

    [SET_SETTINGS] (state, payload) {
      state.settings = payload;
      SettingsService.setSettings(payload);
    }
  },
  actions: {
    [SELECT_SESSION]({ state }, payload) {
      state.selectedSessionId = payload;
    },
    async [DELETE_SESSION]({ state, getters, commit }, payload) {
      const item = payload;

      await SessionStorage.initDB();
      const newSessions = getters.sessions.filter(s => {
        if (item.id == s.id) {
          return false;
        } else {
          return true;
        }
      });

      commit(SET_SESSIONS, newSessions);
      await SessionStorage.deleteSession(item.id);
    },
    async [INSERT_MEDIA]({ commit, getters }, payload) {
      const media = payload;
      await MediaService.insertMedia(media);
      const medias = await MediaService.getAllMedia();
      commit(SET_MEDIAS, medias);
    },
    async [REMOVE_MEDIA]({ commit, getters }, payload) {
      const media = payload;
      await MediaService.deleteMedia(media.id);
      const medias = await MediaService.getAllMedia();
      commit(SET_MEDIAS, medias);
    },
    async  [SET_SINGLE_SETTINGS] ({commit, getters}, payload) {
      const { name, value } = payload;
      const newSettings = {
        ...getters.settings,
        [name]: value
      };
      commit(SET_SETTINGS, newSettings);
    },
  },
  getters: {
    sessions: (state) => state.sessions,
    medias: (state) => state.medias,
    history: (state) => state.history,
    settings: (state)=> state.settings,
    selectedSessionId: (state) => state.selectedSessionId,
    selectedSession: (state) => {
      return state.sessions.find(s => s.id == state.selectedSessionId);
    },
  },
  modules: {
  }
})
