<template>
  <v-row class="number-input" pa-2>
    <span class="label">{{label}}</span>
    <v-text-field label="0" :value="value" hide-details flat solo type="number"></v-text-field>
    <Incdec @increase="onIncrease" @decrease="onDecrease" />
  </v-row>
</template>

<script>
import Incdec from "../components/incdec";

export default {
  components: {
    Incdec
  },
  props: {
    label: {
      default: "Number"
    },
    value: {
      default: 0
    },
    step: {
      default: 1
    },
    min: {
      default: 0
    },
    max: {
      default: Number.MAX_SAFE_INTEGER
    }
  },
  methods: {
    onIncrease: function() {
      const value = this.value + this.step;

      this.$emit("update", value >= this.min ? value : this.min);
    },
    onDecrease: function() {
      const value = this.value - this.step;
      console.log("Log: value", value)

      this.$emit("update", value >= this.min ? value : this.min);
    }
    //MARK: Methods
  }
};
</script>

