import { openDB } from "idb";
import { DEMO_SESSION_LIST } from "./sessions.model";
import { DEMO_MEDIA_LIST } from "./media.model";

export const MEDIA_STORE = 'MEDIA_STORE';


export const DB_NAME = 'EXTIMER_DB';

export const SESSION_STORE = 'SESSION_STORE';
export const HISTORY_STORE = 'HISTORY_STORE';

export class IDBServiceClass {
  constructor() {

  }

  async initDB() {
    if (this.db == null) {
      console.log("Log: IDBServiceClass -> initDB -> this.db", this.db);
      this.db = await openDB(DB_NAME, 1, {
        async upgrade(db) {
          console.log('IndexedDb Upgrade Called');
          await db.createObjectStore(SESSION_STORE, {
            keyPath: 'id',
          });
          await db.createObjectStore(HISTORY_STORE, {
            keyPath: 'date',
          });
          await db.createObjectStore(MEDIA_STORE, {
            keyPath: 'id',
          });
        },
      });
      await this.insertInitialData();
      return this.db;
    } else {
      return this.db;
    }
  }
  async insertInitialData() {
    const sessionData = await this.db.getAll(SESSION_STORE);

    if (sessionData.length == 0) {
      await Promise.all(DEMO_SESSION_LIST.map(session => this.db.add(SESSION_STORE, session)))
      console.log('Data inserted !', await this.db.getAll(SESSION_STORE));
    } else {
      console.log('DATA EXIST', await this.db.getAll(SESSION_STORE));
    }

    const mediaList = await this.db.getAll(MEDIA_STORE);

    if (mediaList.length == 0) {
      await Promise.all(DEMO_MEDIA_LIST.map(media => this.db.add(MEDIA_STORE, media)))
    } else {
      console.log('DATA EXIST', await this.db.getAll(MEDIA_STORE));
    }
  }

}

export const IDBService = new IDBServiceClass();





