<template>
  <v-col class="group" :class="{
    'collapsed': group._collapsed,
    'COLLAPSED_ALL': all_collapsed
  }">
    <!-- <div class="gxr-title" @click="excolToggle(!group._collapsed, group)">
      <v-btn depressed class="excol-button">
        <v-icon v-if="group._collapsed">mdi-plus</v-icon>
        <v-icon v-else>mdi-minus</v-icon>
      </v-btn>
      <span>Group {{group.name}}</span>
      <v-btn depressed class="delete-button">
        <v-icon>mdi-delete-outline</v-icon>
      </v-btn>
    </div>-->
    <GxrTitle
      entity_type="G"
      :gxr="group"
      @bar_click="excolToggle(!group._collapsed, group)"
      @delete="onGroupDelete"
    />
    <div class="form-group">
      <v-text-field
        label="Name"
        outlined
        :value="group.name"
        @input="e => onChange('name', e)"
        hide-details
      ></v-text-field>
      <!-- <v-text-field label="Desciption" outlined :value="group.description" @input="e => onChange('description', e)" hide-details></v-text-field> -->
    </div>
    <v-row>
      <Duration label="Recovery" :value="group.recovery" @update="value => $emit('update', {name: 'recovery', value, item: group})" />
      <NumberInput label="Repeat" :value="group.repeat" @update="value => $emit('update', {name: 'repeat', value, item: group})" />
    </v-row>
    <v-row class="allcolexpand-buttons">
      <!-- <div class="duration">
        <v-icon>mdi-timer-outline</v-icon>
        <div class="duration--text">
          <div>{{SessionFuncs.getDurationString(group.total_duration)}}</div>
        </div>
      </div> -->
      <v-btn outlined x-small>
        <span v-if="all_collapsed" @click="onExpandAll">Expand All</span>
        <span v-else @click="onCollapseAll">Collapse All</span>
      </v-btn>
    </v-row>
    <div class="exercise-list">
      <v-row v-for="exercise of group.exercises" :key="exercise.id">
        <ExerciseComponent
          v-if="exercise.type == 'exercise'"
          :exercise="exercise"
          @update="onExerciseUpdated"
          @delete="onExerciseDeleted"
        ></ExerciseComponent>
        <RestComponent
          v-if="exercise.type == 'rest'"
          :rest="exercise"
          @update="e => onExerciseUpdated({...e, item: exercise})"
          @delete="onExerciseDeleted"
        ></RestComponent>
      </v-row>
    </div>
    <v-row class="adding-panel">
      <div class="subtitle">Add to Group</div>
      <div class="buttons">
        <v-btn @click="onAddExercise" class="add-exercise-button">
          <v-icon left>mdi-plus</v-icon>Exercise
        </v-btn>
        <v-btn @click="onAddRest" class="add-rest-button">
          <v-icon left>mdi-plus</v-icon>Rest
        </v-btn>
      </div>
    </v-row>
  </v-col>
</template>

<script>
import Duration from "./duration";
import NumberInput from "./number-input";
import ExerciseComponent from "./exercise";
import RestComponent from "./rest";
import {
  ExcollapseMixin,
  ExcollapseHostMixin
} from "../mixins/excollapse.mixin";
import GxrTitle from "./gxr-title";
import { SessionFuncs } from '../models/sessions.model';

export default {
  components: {
    Duration,
    NumberInput,
    ExerciseComponent,
    RestComponent,
    GxrTitle
  },
  props: ["group"],
  mixins: [ExcollapseMixin(), ExcollapseHostMixin("group", "exercises")],
  data: function() {
    return {
      SessionFuncs: SessionFuncs,
    }
  },
  methods: {
    onAddExercise: function() {
      this.$emit("add_exercise", this.group, this.nextCollapse);
    },
    onAddRest: function() {
      this.$emit("add_rest", this.group, this.nextCollapse);
    },
    onExerciseUpdated: function({ name, value, item }) {
      this.$emit("update", {
        name: "exercises",
        value: this.group.exercises.map(e => {
          if (e.id == item.id) {
            return {
              ...e,
              [name]: value
            };
          } else {
            return e;
          }
        }),
        item: this.group
      });
    },
    onGroupDelete: function() {
      this.$emit("delete", this.group);
    },
    onExerciseDeleted: function(item) {
      this.$emit("update", {
        name: "exercises",
        value: this.group.exercises.filter(e => {
          if (e.id == item.id) {
            return false;
          } else {
            return true;
          }
        }),
        item: this.group
      });
    },
    onChange: function(name, value) {
      this.$emit("update", {
        name,
        value,
        item: this.group
      });
    }
    //MARK: METHODS
  }
};
</script>