<script>
import { SELECT_SESSION, DELETE_SESSION } from "../../store/index";
import { formatRelative } from "date-fns";
import { createHistory } from "../../models/history.model";

export default {
  data: function() {
    return {
      deleteDialog: false,

      selectedSession: null,

      headers: [
        {
          text: "Date",
          value: "date"
        },
        { text: "Name", value: "name" },
        { text: "Status", value: "status" }
      ],

      today: new Date()
    };
  },
  computed: {
    history: function() {
      return this.$store.getters.history;
    }
  },
  methods: {
    formatDate: function(item) {
      return formatRelative(new Date(item.date), this.today);
    },
    getStatus: function(item = createHistory()) {
      return ((item.session_completion / item.session_duration) * 100).toFixed(
        1
      );
    }
    //MARK: METHODS
  }
};
</script>

